"use strict";
import { createColumnHelper } from "@tanstack/react-table";
import { InterfaceElementName } from "@uniswap/analytics-events";
import QueryTokenLogo from "components/Logo/QueryTokenLogo";
import { Cell } from "components/Table/Cell";
import {
  ClickableHeaderRow,
  EllipsisText,
  HeaderArrow,
  HeaderCell,
  HeaderSortText,
  TableText
} from "components/Table/styled";
import { MAX_WIDTH_MEDIA_BREAKPOINT } from "components/Tokens/constants";
import {
  TokenSortMethod,
  exploreSearchStringAtom,
  filterTimeAtom,
  sortAscendingAtom,
  sortMethodAtom,
  useSetSortMethod
} from "components/Tokens/state";
import { MouseoverTooltip, TooltipSize } from "components/Tooltip";
import { OrderDirection, getTokenDetailsURL, unwrapToken } from "graphql/data/util";
import useSimplePagination from "hooks/useSimplePagination";
import { useAtomValue } from "jotai/utils";
import { memo, useMemo } from "react";
import { Trans } from "react-i18next";
import { TABLE_PAGE_SIZE, giveExploreStatDefaultValue } from "state/explore";
import { useTopTokens as useRestTopTokens } from "state/explore/topTokens";
import { Flex, Text, styled, useMedia } from "ui/src";
import { Plus } from "ui/src/components/icons";
import { useEnabledChains } from "uniswap/src/features/chains/hooks/useEnabledChains";
import { UniverseChainId } from "uniswap/src/features/chains/types";
import { fromGraphQLChain, toGraphQLChain } from "uniswap/src/features/chains/utils";
import { buildCurrencyId } from "uniswap/src/utils/currencyId";
import { getChainIdFromChainUrlParam } from "utils/chainParams";
import { NumberType, useFormatter } from "utils/formatNumbers";
import { Table } from "./Table";
const TableWrapper = styled(Flex, {
  m: "0 auto",
  maxWidth: MAX_WIDTH_MEDIA_BREAKPOINT
});
function TokenDescription({ token }) {
  return <Flex>
      <Flex row gap="$gap8" alignItems="center" justifyContent="flex-start">
        <Flex row alignItems="center" gap={"$gap8"} pr="$spacing4">
          <QueryTokenLogo token={token} size={40} />{" "}
          <Flex ml={-15} height={40} width={40} borderRadius={"50%"} backgroundColor="$neutral1" centered>
            <img height={25} width={25} src="/logo.svg" alt="aborean-logo" />
          </Flex>
        </Flex>
        <Flex flexDirection="column">
          <Flex gap="$gap8">
            <EllipsisText data-testid="token-names"> {token.symbol} / ABX </EllipsisText>
          </Flex>
          <Text fontSize={"$small"} color={"$blueBase"}>
            Basic Volatile
          </Text>
        </Flex>
      </Flex>
    </Flex>;
}
export const PoolsTable = memo(function PoolsTable2() {
  const { topTokens, tokenSortRank, isLoading, sparklines, isError } = useRestTopTokens();
  console.log(useRestTopTokens());
  const { page, loadMore } = useSimplePagination();
  return <TableWrapper data-testid="top-tokens-explore-table">
      <TokenTable
    tokens={topTokens?.slice(0, page * TABLE_PAGE_SIZE)}
    tokenSortRank={tokenSortRank}
    sparklines={sparklines}
    loading={isLoading}
    loadMore={loadMore}
    error={isError}
  />
    </TableWrapper>;
});
const HEADER_TEXT = {
  [TokenSortMethod.PRICE]: "Volume",
  [TokenSortMethod.FULLY_DILUTED_VALUATION]: "Fees",
  [TokenSortMethod.HOUR_CHANGE]: "Total Rewards",
  [TokenSortMethod.DAY_CHANGE]: "TVL",
  [TokenSortMethod.VOLUME]: "APR"
};
export const HEADER_DESCRIPTIONS = {
  [TokenSortMethod.PRICE]: void 0,
  [TokenSortMethod.DAY_CHANGE]: void 0,
  [TokenSortMethod.HOUR_CHANGE]: void 0,
  [TokenSortMethod.FULLY_DILUTED_VALUATION]: <Trans i18nKey="stats.fdv.description" />,
  [TokenSortMethod.VOLUME]: <Trans i18nKey="stats.volume.description" />
};
function TokenTableHeader({
  category,
  isCurrentSortMethod,
  direction
}) {
  const handleSortCategory = useSetSortMethod(category);
  return <Flex width="100%">
      <MouseoverTooltip
    disabled={!HEADER_DESCRIPTIONS[category]}
    size={TooltipSize.Max}
    text={HEADER_DESCRIPTIONS[category]}
    placement="top"
  >
        <ClickableHeaderRow justifyContent="flex-end" onPress={handleSortCategory} group>
          <HeaderArrow orderDirection={direction} size={14} opacity={isCurrentSortMethod ? 1 : 0} />
          <HeaderSortText active={isCurrentSortMethod} variant="body3">
            {HEADER_TEXT[category]}
          </HeaderSortText>
        </ClickableHeaderRow>
      </MouseoverTooltip>
    </Flex>;
}
function TokenTable({
  tokens,
  tokenSortRank,
  sparklines,
  loading,
  error,
  loadMore
}) {
  const { formatFiatPrice, formatNumber, formatDelta } = useFormatter();
  const { defaultChainId } = useEnabledChains();
  const sortAscending = useAtomValue(sortAscendingAtom);
  const orderDirection = sortAscending ? OrderDirection.Asc : OrderDirection.Desc;
  const sortMethod = useAtomValue(sortMethodAtom);
  const filterString = useAtomValue(exploreSearchStringAtom);
  const timePeriod = useAtomValue(filterTimeAtom);
  const tokenTableValues = useMemo(
    () => tokens?.map((token, i) => {
      const delta1d = token.pricePercentChange1Day?.value;
      const currCurrencyId = buildCurrencyId(fromGraphQLChain(token.chain) ?? UniverseChainId.Mainnet, token.address);
      const tokenSortIndex = tokenSortRank[currCurrencyId];
      const chainId = getChainIdFromChainUrlParam(token.chain.toLowerCase());
      const unwrappedToken = chainId ? unwrapToken(chainId, token) : token;
      return {
        index: tokenSortIndex,
        tokenDescription: <TokenDescription token={unwrappedToken} />,
        price: giveExploreStatDefaultValue(token.price?.value),
        testId: `token-table-row-${unwrappedToken.address}`,
        percentChange1d: <TableText>{formatDelta(delta1d)}</TableText>,
        fdv: giveExploreStatDefaultValue(token.fullyDilutedValuation?.value),
        volume: giveExploreStatDefaultValue(token.volume?.value),
        link: getTokenDetailsURL({
          address: unwrappedToken.address,
          chain: toGraphQLChain(chainId ?? defaultChainId)
        }),
        analytics: {
          elementName: InterfaceElementName.TOKENS_TABLE_ROW,
          properties: {
            chain_id: chainId,
            token_address: token.address,
            token_symbol: token.symbol,
            token_list_index: i,
            token_list_rank: tokenSortIndex,
            token_list_length: tokens.length,
            time_frame: timePeriod,
            search_token_address_input: filterString
          }
        },
        linkState: { preloadedLogoSrc: token.logo }
      };
    }) ?? [],
    [defaultChainId, filterString, formatDelta, sparklines, timePeriod, tokenSortRank, tokens]
  );
  const showLoadingSkeleton = loading || !!error;
  const media = useMedia();
  const columns = useMemo(() => {
    const columnHelper = createColumnHelper();
    const filteredColumns = [
      // !media.lg
      //   ? columnHelper.accessor((row) => row.index, {
      //       id: 'index',
      //       size: 60,
      //       header: () => (
      //         <HeaderCell justifyContent="flex-start">
      //           <Text variant="body3" color="$neutral2">
      //             #
      //           </Text>
      //         </HeaderCell>
      //       ),
      //       cell: (index) => (
      //         <Cell height="100%" justifyContent="flex-start" loading={showLoadingSkeleton}>
      //           <TableText>{index.getValue?.()}</TableText>
      //         </Cell>
      //       ),
      //     })
      //   : null,
      columnHelper.accessor((row) => row.tokenDescription, {
        id: "tokenDescription",
        size: media.lg ? 150 : 300,
        header: () => <HeaderCell justifyContent="flex-start">
            <Text variant="body3" color="$neutral2" fontWeight="500">
              Liquidity Pools
            </Text>
          </HeaderCell>,
        cell: (tokenDescription) => <Cell height="100%" justifyContent="flex-start" loading={showLoadingSkeleton} testId="name-cell">
            <TableText>{tokenDescription.getValue?.()}</TableText>
          </Cell>
      }),
      columnHelper.accessor((row) => row.price, {
        id: "price",
        maxSize: 140,
        header: () => <HeaderCell justifyContent="flex-end">
            <TokenTableHeader
          category={TokenSortMethod.PRICE}
          isCurrentSortMethod={sortMethod === TokenSortMethod.PRICE}
          direction={orderDirection}
        />
          </HeaderCell>,
        cell: (price) => <Cell height="100%" loading={showLoadingSkeleton} testId="fees-cell">
            <Flex height={"100%"} width={"100%"} my={"$spacing1"} flexDirection="column" justifyContent="space-between">
              <TableText textAlign="right">
                {
          /* A simple 0 price indicates the price is not currently available from the api */
        }
                {price.getValue?.() === 0 ? "-" : formatFiatPrice({ price: price.getValue?.(), type: NumberType.FiatTokenPrice })}
              </TableText>

              <Flex gap={"$gap2"} justifyContent="flex-end">
                <TableText fontSize={"$micro"} textAlign="right" color={"$neutral3"}>
                  55.34 ABX
                </TableText>
                <TableText fontSize={"$micro"} textAlign="right" color={"$neutral3"}>
                  136,971.19 ABX
                </TableText>
              </Flex>
            </Flex>
          </Cell>
      }),
      columnHelper.accessor((row) => row.percentChange1d, {
        id: "percentChange1d",
        maxSize: 120,
        header: () => <HeaderCell justifyContent="flex-end">
            <TokenTableHeader
          category={TokenSortMethod.DAY_CHANGE}
          isCurrentSortMethod={sortMethod === TokenSortMethod.DAY_CHANGE}
          direction={orderDirection}
        />
          </HeaderCell>,
        cell: (percentChange1d) => <Cell height="100%" loading={showLoadingSkeleton} testId="fees-cell">
            <Flex height={"100%"} width={"100%"} my={"$spacing1"} flexDirection="column" justifyContent="space-between">
              <TableText fontSize={"$micro"} textAlign="right" color={"$neutral3"}>
                {percentChange1d.getValue?.()}
              </TableText>

              <Flex gap={"$gap2"} justifyContent="flex-end">
                <TableText fontSize={"$micro"} textAlign="right" color={"$neutral3"}>
                  55.34 ABX
                </TableText>
                <TableText fontSize={"$micro"} textAlign="right" color={"$neutral3"}>
                  136,971.19 ABX
                </TableText>
              </Flex>
            </Flex>
          </Cell>
      }),
      columnHelper.accessor((row) => row.fdv, {
        id: "fdv",
        maxSize: 120,
        header: () => <HeaderCell justifyContent="flex-end">
            <TokenTableHeader
          category={TokenSortMethod.FULLY_DILUTED_VALUATION}
          isCurrentSortMethod={sortMethod === TokenSortMethod.FULLY_DILUTED_VALUATION}
          direction={orderDirection}
        />
          </HeaderCell>,
        cell: (fdv) => <Cell height="100%" loading={showLoadingSkeleton} testId="fees-cell">
            <Flex height={"100%"} width={"100%"} my={"$spacing1"} flexDirection="column" justifyContent="space-between">
              <EllipsisText textAlign="right">
                {formatNumber({ input: fdv.getValue?.(), type: NumberType.FiatTokenStats })}
              </EllipsisText>

              <Flex gap={"$gap2"} justifyContent="flex-end">
                <TableText fontSize={"$micro"} textAlign="right" color={"$neutral3"}>
                  55.34 ABX
                </TableText>
                <TableText fontSize={"$micro"} textAlign="right" color={"$neutral3"}>
                  136,971.19 ABX
                </TableText>
              </Flex>
            </Flex>
          </Cell>
      }),
      columnHelper.accessor((row) => row.volume, {
        id: "volume",
        maxSize: 150,
        header: () => <HeaderCell>
            <TokenTableHeader
          category={TokenSortMethod.VOLUME}
          isCurrentSortMethod={sortMethod === TokenSortMethod.VOLUME}
          direction={orderDirection}
        />
          </HeaderCell>,
        cell: (volume) => <Cell height="100%" loading={showLoadingSkeleton} testId="fees-cell">
            <Flex height={"100%"} width={"100%"} my={"$spacing1"} flexDirection="column" justifyContent="space-between">
              <EllipsisText textAlign="right">
                {formatNumber({ input: volume.getValue?.(), type: NumberType.FiatTokenStats })}
              </EllipsisText>

              <Flex row justifyContent="flex-end">
                <Flex
          py={"$spacing8"}
          px={"$spacing12"}
          borderRadius={"$rounded12"}
          backgroundColor={"$slate"}
          row
          gap={"$gap8"}
          centered
          cursor="pointer"
          hoverStyle={{ backgroundColor: "$surface2" }}
        >
                  <Plus color={"$accent1"} size={"$icon.12"} />
                  <TableText fontSize={"$micro"} textAlign="right" color={"$neutral2"}>
                    New Deposite
                  </TableText>
                </Flex>
              </Flex>
            </Flex>
          </Cell>
      })
    ];
    return filteredColumns.filter((column) => Boolean(column));
  }, [formatFiatPrice, formatNumber, orderDirection, showLoadingSkeleton, sortMethod, media]);
  return <Table
    columns={columns}
    data={tokenTableValues}
    loading={loading}
    error={error}
    loadMore={loadMore}
    maxWidth={1200}
    defaultPinnedColumns={["index", "tokenDescription"]}
  />;
}
