"use strict";
import { useState } from "react";
import styled from "styled-components";
import { Flex, Separator, Text, styled as UiStyled } from "ui/src";
import { Check, Plus } from "ui/src/components/icons";
const Button = styled.button`
  padding: 8px 12px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  hover {
    color: red;
  }
`;
const TextCotainer = UiStyled(Text, {
  variant: "body3",
  color: "$neutral1",
  cursor: "pointer",
  hoverStyle: {
    color: "$accent1"
  }
});
const FIlterContainer = UiStyled(Flex, {
  row: true,
  alignItems: "center",
  gap: "$spacing12",
  borderStyle: "solid",
  borderWidth: 1,
  borderColor: "$surface2",
  borderRadius: "$rounded12"
});
export default function FilterPools() {
  const [activeFilter, setActiveFilter] = useState({
    Basic: false,
    Consetrated: false,
    Stable: false,
    Volatile: false,
    Incetivized: false,
    "Low TVL": false,
    ALM: false
  });
  const GenerateFilterButton = function({ type, divider }) {
    const handleFilter = function(value) {
      switch (type) {
        case "Basic":
          setActiveFilter({
            ...activeFilter,
            Consetrated: !value
          });
          return;
        case "Consetrated":
          setActiveFilter({
            ...activeFilter,
            Basic: !value
          });
          return;
        case "Stable":
          setActiveFilter({
            ...activeFilter,
            Volatile: !value
          });
          return;
        case "Volatile":
          setActiveFilter({
            ...activeFilter,
            Stable: !value
          });
          return;
        default:
          setActiveFilter({
            ...activeFilter,
            [type]: value
          });
      }
    };
    return <>
        {divider && <Separator vertical height={"100%"} />}
        <Button onClick={() => handleFilter(!activeFilter[type])}>
          <TextCotainer>{type}</TextCotainer> {activeFilter[type] ? <Checkmark /> : <PlusSign />}
        </Button>
      </>;
  };
  return <Flex row gap={"$spacing12"}>
      <FIlterContainer>
        <GenerateFilterButton type={"Basic"} />
        <GenerateFilterButton type={"Consetrated"} divider />
      </FIlterContainer>

      <FIlterContainer>
        <GenerateFilterButton type={"Stable"} />
        <GenerateFilterButton type={"Volatile"} divider />
      </FIlterContainer>

      <FIlterContainer>
        <GenerateFilterButton type={"Incetivized"} />
      </FIlterContainer>

      <FIlterContainer>
        <GenerateFilterButton type={"Low TVL"} />
      </FIlterContainer>

      <FIlterContainer>
        <GenerateFilterButton type={"ALM"} />
      </FIlterContainer>
    </Flex>;
}
const Checkmark = function() {
  return <Check color={"$greenVibrant"} size={"$icon.12"} />;
};
const PlusSign = function() {
  return <Plus color={"$accent1"} size={"$icon.12"} />;
};
